<template>
    <div class="list-page">
         <!-- 头 -->
        <van-nav-bar title="我的战绩" fixed :border="false"/>

        <div class="list-top">
            <div class="list-top-img">
                <img src="../assets/imgs/list-top.png" alt="">
            </div>
            <div class="list-top-content">
                <div class="list-top-content-title">今日总收益</div>
                <div class="list-top-content-money">2565蚪金</div>
            </div>
        </div>

        <!-- tab栏 -->
        <div class="list-tabs">
            <div class='list-tabs-item' :class="{'list-tabs-item-active':tabActive == 1}" @click="tabClick(1)">
                <span>猜涨跌</span>
            </div>
            <div class='list-tabs-item' :class="{'list-tabs-item-active':tabActive == 2}" @click="tabClick(2)">
                <span>幸运转盘</span>
            </div>
        </div>


        <!-- 幸运转盘 -->
        <div class="list-tab-box">
            <div class="list-tab-content">
                <div class="list-tab-content-title">
                    <img src="../assets/imgs/list-record.png" alt="">
                    <span>历史战绩</span>
                </div>
                <div class="list-tab-content-list">
                    <van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="getDrawList">
                        <div v-for="(item,index) in drawList" :key="index" class='list-tab-content-list-item'>
                            <div class="list-tab-content-list-item-text">
                                <div class="list-tab-content-list-item-text-left">幸运转盘</div>
                                <div class="list-tab-content-list-item-text-right">
                                    <span v-if="item.game_raword.id == 3 || item.game_raword.id == 4 || item.game_raword.id == 5 || item.game_raword.id == 7">+{{parseInt(item.game_raword.prize_money)}}{{item.game_raword.prize}}</span>
                                    <span v-if="item.game_raword.id == 1 || item.game_raword.id == 2">{{item.game_raword.prize}}一台</span>
                                    <span v-if="item.game_raword.id == 6 || item.game_raword.id == 8">{{item.game_raword.prize}}</span>
                                </div>
                            </div>
                            <div class="list-tab-content-list-item-tip">
                                <div class="list-tab-content-list-item-tip-left">{{item.add_time}}</div>
                                <div class="list-tab-content-list-item-tip-right">抽奖金额：0.5</div>
                            </div>
                        </div>
                    </van-list>
                   
                </div>
            </div>
        </div>
        
    </div>
</template>



<script>

import { mapState } from 'vuex'
import { mapMutations } from 'vuex'

import { Dialog  } from 'vant';

export default {
    data(){
        return{
            tabActive:1,
            //抽奖记录
            drawList:[],
            loading: false,
            finished: false,
            page:1,
        }
    },
    computed:{
        ...mapState(['user_info']),
    },
    created(){
        this.utils.info('user_info...',this.user_info);
        if(this.$route.params.id){
			this.utils.info('有id...已登录...');
            this.user_info.user_id = this.$route.params.id;
            
            //获取用户信息
		    this.getUserInfo('1');
		}else{
			this.utils.info('没有id...未登录...');
            // this.user_info.user_id = 1;
            
            Dialog.alert({
				title: '系统提示',
				message: '请先登录...'
			}).then(() => {
				// on close
				this.utils.info('返回app...');
				this.$bridge.callhandler('goBack', {tag:"返回app"}, (data) => {})
				
			});
        }
        this.utils.info('user_info...',this.user_info);
    },
    mounted(){

    },
    methods:{
        //返回
		goBack(){
			this.utils.info('返回app...');
			this.$bridge.callhandler('goBack', {tag:"返回app"}, (data) => {})
        },
        //获取用户信息 type 1 去请求奖品列表和用户列表 2不去
		getUserInfo(type){
            let user_id = 'dy' + this.user_info.user_id + this.utils.randomNumber(4);
			this.http.post(this.http._HOST + 'login/user_detail',{user_id:user_id}).then(result =>{
                this.utils.info('获取用户信息...',result);
                if(result.success){
                    let data = result.data;

                    this.user_info.dw_money = data.dw_money;
                    this.user_info.pay_password = data.pay_password;
                    this.user_info.token = data.token;
                    this.user_info.free_password = data.free_password;

                    if(type == '1'){
                        //获取转盘抽奖记录
                        this.getDrawList();

                        //获取中奖用户列表
                        //this.getWinList();
                    }
                    
                }
			})
        },
        //切换类型
        tabClick(index){
            if(this.tabActive == index){
                return false;
            }

            this.tabActive = index;
        },
        //获取转盘抽奖记录
        getDrawList(){
            
            let option = {
                token: this.user_info.token,
                page: this.page
            }
            this.http.post(this.http._HOST + 'turntable/history',option).then(result =>{
				this.utils.info('获取转盘抽奖记录...',result);
				if(result.success){
                    
                    if(result.data.length == 0){
                        this.finished = true;
                    }else{
                        
                        this.drawList.concat(result.data);
                        this.page++;
                    }
                    
                    this.loading = false;

                
				}
			})
        }
    }
}
</script>



<style lang="scss" scoped>
    .list-page{
        background: #f6f6fa;
        width:100%;
        height:100%;

        .van-nav-bar{
            background: transparent;
        }
        .van-nav-bar__title{
            font-size:18px;
            color:#fff;
        }
        .van-nav-bar .van-icon{
            font-size:20px;
            color:#353535;
        }

        .list-top{
            position: relative;
        }
        .list-top-img{
           
        }
        .list-top-img img{
            display: block;
            width:100%;
        }
        .list-top-content{
            position: absolute;
            top:50%;
            left:50%;
            transform: translate(-50%,-50%);
        }
        .list-top-content-title{
           font-size:16px;
           color:#FFE9C2;
           text-align: center;
           padding-bottom: 10px;
        }
        .list-top-content-money{
           font-size:24px;
           color:#fff;
           text-align: center;
        }

        .list-tabs{
            display: flex;
            align-items: center;
        }
        .list-tabs-item{
            width:50%;
            height:40px;
            line-height:40px;
            text-align: center;
            font-size:14px;
            color:#999;
            box-sizing: border-box;
        }
        .list-tabs-item-active{
            font-size:16px;
            font-weight: bold;
            color:#333;
        }
        .list-tabs-item-active span{
           display: inline-block;

           border-bottom: 2PX solid #FF8E20;
        }

        .list-tab-box{
            padding:25px 15px;
        }
        .list-tab-content{
            padding:15px;
            border-radius: 15px;
            background: #fff;
        }

        .list-tab-content-title{
            display: flex;
            align-items: center;
        }
        .list-tab-content-title img{
            display: inline-block;
            width:18px;
            margin-right:5px;
        }
        .list-tab-content-title span{
            font-size:16px;
            color:#333;
            font-weight: bold;
        }

        .list-tab-content-list-item{
            padding:15px 0;
            border-bottom: 1PX solid #d5d5d5;
        }
        .list-tab-content-list-item-text{
            display: flex;
            align-items: center;
            justify-content: space-between;
            font-size:16px;
            padding-bottom: 10px;
        }
        .list-tab-content-list-item-text-left{
           color:#333;
        }
        .list-tab-content-list-item-text-right{
           color:#FF8E20;
        }
        .list-tab-content-list-item-tip{
            display: flex;
            align-items: center;
            justify-content: space-between;
            color:#999;
        }
        .list-tab-content-list-item-text-left{
            font-size:12px;
        }
        .list-tab-content-list-item-text-right{
            font-size:14px;
        }
    }
</style>


